.Portfolio-card1 {
	/* width: 1163px; */
	background: #fff;
	border-radius: 10px;
	overflow: hidden;

	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-box;
	display: -ms-flexbox;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	padding: 10px;
}
.LeftSection {
  border-radius: 10px;
  border: 1px solid rgb(105, 83, 83);
  margin-bottom:10px;
}
.RightSection {
  /* background: black; */
  border-radius: 10px;
  /* border: 1px solid rgb(105, 83, 83); */
}
